import { Component, OnInit, Output } from "@angular/core";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import { AddvlComponent } from "../addvl/addvl.component";
import { Services } from "../service/service";
import { HttpClient } from "@angular/common/http";
@Component({
  selector: "app-vatlieu",
  templateUrl: "./vatlieu.component.html",
  styleUrls: ["./vatlieu.component.css"]
})
export class VatlieuComponent implements OnInit {
  public vatlieu: any = [];
  public root = [];
  selectedItem: any = {};
  abc = { background: "#117a8b", color: "white" };
  def = { background: "none", color: "black" };
  cungcap = true;
  xyz = true;

  constructor(private modalService: NgbModal, private test: Services) {}

  ngOnInit() {
    this.chucnang();
  }

  chucnang() {
    this.test.getvatlieu("a").subscribe((data: any) => {
      data.forEach(element => {
        element.Id = parseInt(element.Id);
      });
      this.vatlieu = data;
      this.flatToTreeArray(this.vatlieu, "Id", "IdParent");
      console.log(this.vatlieu);
    });
  }

  addvl() {
    let modalref = this.modalService.open(AddvlComponent, {
      ariaLabelledBy: "modal-basic-title"
    });
    if (this.cungcap) {
      modalref.componentInstance.add.IdParent = this.selectedItem.IdParent;
    } else {
      modalref.componentInstance.add.IdParent = this.selectedItem.Id;
    }
    modalref.result.then(
      result => {
        this.test.addVatLieu(result).subscribe(data => {
          console.log(data);
          this.chucnang();
          alert("Đã lưu!");
        });
      },
      reason => {
        alert(this.getDismissReason(reason));
      }
    );
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return "Đã hủy!";
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return "Đã hủy!";
    } else {
      return "Đã hủy!";
    }
  }

  flatToTreeArray(list, id: string, idp: string) {
    let finalData = [];
    list.forEach(element => {
      element.child = [];
    });
    list.forEach(element => {
      if (element.IdParent !== null) {
        let a = list.filter((e: any) => e[id] === element[idp])[0];
        if (a) {
          a.child.push(element);
        } else {
          element = undefined;
        }
      } else {
        finalData.push(element);
      }
    });
    //console.log(finalData);
    this.root = finalData;
    // console.log(this.root);
  }

  showData(id: any, state) {
    this.xyz = false;
    const index = this.vatlieu.findIndex(a => a.Id === id);
    this.selectedItem = this.vatlieu[index];
    this.selectedItem.i = index;
    this.vatlieu.forEach(element => {
      element.state = false;
    });
    console.log(this.selectedItem.Id);
    if (!state) {
      this.selectedItem.state = !state;
    } else {
      this.selectedItem = {};
    }
  }

  removeVatlieu() {
    if (this.selectedItem.Id != undefined) {
      this.test.deleteVatLieu(this.selectedItem.Id).subscribe(data => {
        alert('Đã xóa!');
        this.selectedItem = {};
        this.xyz = true;
        this.chucnang();
      });
    }
  }

  suaVatlieu() {
    let repair = {
      Ten : this.selectedItem.Ten,
      Ma : this.selectedItem.Ma,
      IdParent : this.selectedItem.IdParent,
      DVT : this.selectedItem.DVT,
      NCC : this.selectedItem.NCC,
      GhiChu : this.selectedItem.GhiChu
    }
    this.test.repairVatLieu(this.selectedItem.Id, repair).subscribe(data => {
      this.chucnang();
      alert('Đã sửa!');
      this.selectedItem = {};
      this.xyz = true;
      console.log(this.selectedItem.Id)
    });
  }
}
